/* INDEX.CSS | CSS responsável pela manutenção geral do aplicativo. */
html {
    width: 100%;
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Barlow Semi Condensed";

    width: 100%;
    height: 100%;

    overflow-x: hidden;
    user-select: none;
    -ms-user-select: none;
    -webkit-user-select: none;
}

#root{
    width: 100%;
    height: 100%;
    overflow-x: hidden;
}

@media(max-height: 510px){
    html {
        height: 800px;
    }

    body {
        height: 800px;
    }
}

@media(max-height: 600px) and (min-width: 1000px) {
    html {
        height: 900px;
    }

    body {
        height: 900px;
    }
}

/* Importação de tipo de fonte. */
@font-face {
    font-family: 'Barlow Semi Condensed';
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: url(https://fonts.gstatic.com/s/barlowsemicondensed/v13/wlpigxjLBV1hqnzfr-F8sEYMB0Yybp0mudRfp66_B2sl.woff2) format('woff2');
    unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}